import React from 'react';
import Grid from '@mui/material/Grid';
import TabsComp from '../Common/Tab';
import TabPane from '../Common/TabPane';
import style from './Tabs.module.css';
import Potal from '../../images/Portal.PNG';
import Equipment from '../../images/Equipment.PNG';
import AssetManagement from '../../images/AssetManagement.PNG';
import Inventory from '../../images/Inventory.PNG';
import Sales from '../../images/Sales.PNG';
const Tabs = () => {
  return (
    <Grid container spacing={2} className={style.pt}>
      <Grid item xs={3}>
        <div>
          <img src={Potal} alt="PortalDashboard" />
          <div>PortalDashboard</div>
        </div>
        <div>
          <img src={Equipment} alt="Equipment" />
          <div>Equipment Status</div>
        </div>
        <div>
          <img src={AssetManagement} alt="AssetManagement" />
          <div>AssetManagement</div>
        </div>
        <div>
          <img src={Inventory} alt="Inventory" />
          <div>Inventory Management</div>
        </div>
        <div>
          <img src={Sales} alt="Sales" />
          <div>Sales</div>
        </div>
      </Grid>
      <Grid item xs={9}>
        <TabsComp>
          <TabPane name="Tab 1" key="1">
            <Grid container spacing={2} className={style.containerTab}>
              <Grid item xs={9}>
                <div className={style.summarySection}>Detail Section</div>
              </Grid>
              <Grid item xs={3}>
                <div className={style.summarySection}>
                  Installed Base Summary
                </div>
              </Grid>
            </Grid>
          </TabPane>
          <TabPane name="Tab 2" key="2">
            Content of Tab Pane 2
          </TabPane>
          <TabPane name="Tab 3" key="3">
            Content of Tab Pane 3
          </TabPane>
        </TabsComp>
      </Grid>
    </Grid>
  );
};
export default Tabs;
